<template>
  <!-- <div class="filter-item" :class="filter" :style="{ backgroundImage: `url(${imageUrl})` }"></div> -->
  <div @click="fire" :class="'filter-item ' + filter" :style="{ backgroundImage: `url(${imageUrl})` }">
      <!-- named slot 사용법 -->
      <slot name="filter"></slot>
      <!-- 
        mitt으로 데이터 전송하는 법
        1. this.emitter.emit()으로 발사하고
        2. this.emitter.on()으로 수신한다
       -->
      <!-- <button @click="fire">버튼</button> -->
      <!-- <slot name="b"></slot> -->

    <!-- 자식 데이터 부모 컴포넌트에 전송 -->
      <!-- <slot :msg="msg"></slot> -->
  </div>
</template>

<script>
export default {
  name: "FilterBox",
  props: {
    imageUrl: String,
    filter: String,
  },
  methods: {
    // fire() {
    //   this.emitter.emit('작명', '데이터');
    // }
    fire() {
      this.emitter.emit('selectedFilter', this.filter);
    }
  },      
  data() {
      return{
          msg: 'hello'
      }
  }
};
</script>

