<template>
  <div class="header">
    <ul class="header-button-left">
      <li @click="step = 0">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step === 1" @click="step++">Next</li>
      <li v-if="step === 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <h4>안녕 {{ $store.state.name }}</h4>
  <br />
  <h4>{{ $store.state.age }} 살</h4>
  <!-- 
    store.js에 있는 state 변경은 관습적으로 직접 수정하지 않는다.
    왜냐하면 직접 수정하다보면 자료형이 잘못 저장되거나 
    잘못된 데이터가 저장될 수가 있는데
    데이터에 접근하느부분이 많을경우
    이런 부분에 대해서 추적이 어렵다.
   -->
  <!-- 
     store의 데이터를 수정하고 싶으면 
     1. store.js에 state 수정방법 정의
     2. 수정하고 싶으면 store.js에 부탁
     mutation의 함수에 접근할 때는 commit을 사용한다.
    -->
  <!-- <button @click="$store.commit('ADD_AGE', 10)">버튼</button> -->
<!-- mapMutations 사용으로 짧게 적을 수 있다. -->
  <button @click="ADD_AGE(10)">버튼</button>

  <p>{{ $store.state.more }}</p>
  <!-- 
    actions의 함수에 접근할 때는 dispatch를 사용한다.
   -->
  <button @click="$store.dispatch('getData')">더보기버튼</button>

  <!-- <p>{{ now() }} {{카운터}}</p> -->
  <!-- 
    computed 에 정의된 함수는 소괄호를 적어주지 않는다.
    필요한 일부의 데이터만 따로 저장해놓을 때 사용.
   -->
  <!-- <p>{{ now2 }} {{카운터}}</p>
  <button @click="카운터++">버튼</button> -->

  <p>{{ 이름 }} {{ 나이 }}</p>

  <PostContainer
    @write="content = $event"
    :imageUrl="imageUrl"
    :step="step"
    :postData="postData"
  />
  <!-- <button @click="step = 0">포스트</button>
  <button @click="step = 1">이미지</button>
  <button @click="step = 2">글쓰기</button> -->
  <br />
  <button @click="more">더보기</button>

  <!-- 
  이미지 업로드 하는법
  1. FileReader() 
  2. URL.createObjectURL()
 -->
  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>
</template>

<script>
import PostContainer from "./components/PostContainer.vue";
import postData from "./assets/insta-posts.js";
import axios from "axios";
import { mapState, mapMutations } from 'vuex';

export default {
  name: "App",
  data() {
    return {
      step: 0,
      postData,
      dataNumber: 0,
      imageUrl: "",
      content: "",
      filter: "",
      카운터: 0,
    };
  },
  components: {
    PostContainer,
  },
  /**
   * computed 안에 함수는 뷰파일이 처음 로드되었을 때
   * 실행되고 값을 간직하고 있다.
   * 컴퓨터 리소스를 절약할 수 있음.
   * 계산 결과 저장용 함수.
   * 함수가 꼭 무언가를 리턴해줘야함.
   */
  computed: {
    // now2() {
    //   return new Date();
    // },
    // name() {
    //   return this.$store.state.name;
    // },
    // mapState를 사용하여 함수형태로 스토어에 접근하는 형식을 짧게 줄여서 쓸 수 있음.
    // ...mapState(['name', 'age']),
    // 스토어에 데이터에 이름을 붙일수 있음.
    ...mapState({ 이름: 'name', 나이: 'age'})
  },
  /**
   * methods안의 함수는 함수가 호출될 때 마다 실행된다.
   */
  methods: {
    ...mapMutations(['setMore', 'CHANGE_LIKE', 'ADD_AGE']),
    now() {
      return new Date();
    },
    publish() {
      const post = {
        name: "Jackson Lee",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.imageUrl,
        likes: 0,
        date: new Date().toString(),
        liked: false,
        content: this.content,
        filter: this.filter,
      };

      this.postData.unshift(post);
      this.step = 0;
    },
    upload(e) {
      const image = e.target.files;
      const imageUrl = URL.createObjectURL(image[0]);
      this.imageUrl = imageUrl;
      this.step = 1;
    },
    checkButtonPush(buttonNumber) {
      switch (buttonNumber) {
        case 1:
          this.isButtonPush[0] = true;
          this.isButtonPush[1] = false;
          this.isButtonPush[2] = false;
          break;
        case 2:
          this.isButtonPush[0] = false;
          this.isButtonPush[1] = true;
          this.isButtonPush[2] = false;
          break;
        case 3:
          this.isButtonPush[0] = false;
          this.isButtonPush[1] = false;
          this.isButtonPush[2] = true;
          break;

        default:
          break;
      }
    },
    more() {
      axios
        .get(`https://codingapple1.github.io/vue/more${this.dataNumber}.json`)
        .then((response) => {
          this.postData.push(response.data);
          if (this.dataNumber === 0) {
            this.dataNumber = 1;
          } else {
            this.dataNumber = 0;
          }
        });
    },
  },
  // mitt을 이용해서 emitter.on으로 데이터를 받을 때는 관습적으로 mounted에서 받는다.
  mounted() {
    this.emitter.on("selectedFilter", (filter) => {
      this.filter = filter;
    });
    // this.emitter.on('작명', (a) => {
    //   console.log(a);
    // });
  },
};
</script>

