<template>
  <div>
    <div v-if="step === 0">
      <PostDetail :post="post" :postIndex="i" v-for="(post, i) in postData" :key="i" />
    </div>

    <!-- 필터선택페이지 -->
    <div v-if="step === 1">
      <div
      :class="selectedFilter"
        class="upload-image"
        :style="{ backgroundImage: `url(${imageUrl})` }"
      ></div>
      <div class="filters">
        <!-- 
          slot 사용법
          1. 하위 컴포넌트에 slot으로 빈태그 생성
          2. <컴포넌트> </컴포넌트> 형식으로 컴포넌트 태그를 만든 뒤 
          사이에 데이터를 입력하면 하위 컴포넌트의 slot으로 데이터가 전송됨
          HTML 내용에만 데이터를 받아서 사용할 수 있고 속성으로는 사용이 불가능
         -->
        <FilterBox v-for="(filter, i) in filters" :key="i" :filter="filter" :imageUrl="imageUrl">
          <!-- named slot 사용법 -->
            <template v-slot:filter>{{ filter }}</template>

            <!-- 자식 데이터 부모 컴포넌트에 전송 -->
            <!-- <template v-slot:default="msg">{{ msg.msg }}</template> -->
            
            <!-- <template v-slot:b>데이터2</template> -->

            <!-- html 태그와 조합해서도 전송 가능 -->
            <!-- <template v-slot:b> <span>데이터2</span> </template> -->

        </FilterBox>

      </div>
    </div>

    <!-- 글작성페이지 -->
    <div v-if="step === 2">
      <div
        :class="selectedFilter"
        class="upload-image"
        :style="{ backgroundImage: `url(${imageUrl})` }"
      ></div>
      <div class="write">
        <textarea
          @input="$emit('write', $event.target.value)"
          class="write-box"
        >
write!</textarea
        >
      </div>
    </div>
  </div>
</template>

<script>
import PostDetail from "./PostDetail.vue";
import FilterBox from "./FilterBox.vue";

export default {
  name: "PostContainer",
  data() {
    return {
      filters: [
        "aden",
        "_1977",
        "brannan",
        "brooklyn",
        "clarendon",
        "earlybird",
        "gingham",
        "hudson",
        "inkwell",
        "kelvin",
        "lark",
        "lofi",
        "maven",
        "mayfair",
        "moon",
        "nashville",
        "perpetua",
        "reyes",
        "rise",
        "slumber",
        "stinson",
        "toaster",
        "valencia",
        "walden",
        "willow",
        "xpro2",
      ],
      selectedFilter: ''
    };
  },
  components: {
    PostDetail,
    FilterBox,
  },
  props: {
    postData: Array,
    step: Number,
    imageUrl: String,
  },
  mounted() {
    this.emitter.on('selectedFilter', filter => {
      this.selectedFilter = filter;
    })
  }
};
</script>

