import axios from 'axios';
import { createStore } from 'vuex';

const store = createStore({
    state() {
        return {
            name: 'Kim',
            age: 20,
            more: {}

        };
    },
    mutations: {
        setMore(state, data) {
            state.more = data;
        },
        CHANGE_LIKE(state, payload) {
            if (payload.liked) {
                payload.likes--;
                payload.liked = !payload.liked;
                return;
            }

            payload.likes++;
            payload.liked = !payload.liked;
        },
        이름변경(state) {
            state.name = 'park';
        },
        ADD_AGE(state, payload) {
            state.age += payload;
        }
    },
    // 모든 ajax 요청들을 정의하는 속성
    actions: {
        // 첫번째 받는 인자는 $store를 뜻한다고 볼 수 있음.
        getData(context) {
            axios.get('https://codingapple1.github.io/vue/more0.json').then(data => {
                context.commit('setMore', data.data);
            });
        }
    }
});

export default store;
